/* src/pages/Projects.css */
.projects {
  padding: 100px 0;
  background-color: var(--light-color);
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--dark-color);
}

.project-section {
  margin-bottom: 60px;
}

.category-title {
  font-size: 1.8rem;
  margin-bottom: 25px;
  color: var(--dark-color);
  border-bottom: 2px solid #eaeaea;
  padding-bottom: 10px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}

.project-card {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.project-image {
  height: 180px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.project-date {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
}

.project-title {
  font-size: 1.2rem;
  padding: 15px 15px 10px;
  margin: 0;
  color: var(--dark-color);
}

.project-description {
  padding: 0 15px;
  font-size: 0.9rem;
  color: var(--text-color);
  margin-bottom: 15px;
  line-height: 1.4;
}

.project-highlights {
  padding: 0 15px;
  margin-bottom: 15px;
}

.highlight-item {
  display: flex;
  margin-bottom: 8px;
  font-size: 0.85rem;
  color: #555;
  line-height: 1.3;
}

.highlight-bullet {
  margin-right: 8px;
  color: var(--primary-color);
  font-weight: bold;
}

.project-tags {
  padding: 0 15px 15px;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.tag {
  background-color: #f0f0f0;
  color: #555;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 0.75rem;
}

.tag:nth-child(4n+1) {
  background-color: #e3f2fd;
  color: #1976d2;
}

.tag:nth-child(4n+2) {
  background-color: #e8f5e9;
  color: #388e3c;
}

.tag:nth-child(4n+3) {
  background-color: #fff8e1;
  color: #ffa000;
}

.tag:nth-child(4n+4) {
  background-color: #f3e5f5;
  color: #7b1fa2;
}

/* Project card top border colors */
.project-card:nth-child(3n+1) {
  border-top: 3px solid #1976d2;
}

.project-card:nth-child(3n+2) {
  border-top: 3px solid #388e3c;
}

.project-card:nth-child(3n+3) {
  border-top: 3px solid #ffa000;
}

/* Project actions section */
.project-actions {
  margin: 0 15px 15px;
  margin-top: auto;
}

.view-details-btn {
  width: 100%;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-weight: 500;
}

.view-details-btn:hover {
  background-color: #4d64d2;
  transform: translateY(-2px);
  color: white;
}

.view-details-btn:after {
  content: "→";
  margin-left: 8px;
  transition: transform 0.2s ease;
}

.view-details-btn:hover:after {
  transform: translateX(3px);
}

/* External link styles */
.external-link-icon {
  display: inline-block;
  margin-left: 5px;
  font-size: 0.85rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .project-image {
    height: 160px;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .category-title {
    font-size: 1.5rem;
  }
  
  .modal-content {
    width: 95%;
    padding: 15px;
  }
}