/* src/pages/Home.css */
.home {
  padding: 100px 0;
  text-align: center;
  position: relative;
  min-height: 90vh; /* Make sure the section is tall enough to fit the arrow */
}

.home-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

.home-content h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: var(--dark-color);
}

.home-content p {
  font-size: 1.2rem;
  color: var(--text-color);
}

.profile-section {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 30px;
}

.profile-image {
  width: 180px;
  height: 180px;
  border-radius: 50%; /* 圓形照片 */
  object-fit: cover;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  border: 3px solid white;
}

/* Scroll Arrow Styles */
.scroll-arrow-container {
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 30px;
}

.scroll-arrow {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  animation: bounce 2s infinite;
}

.scroll-arrow span {
  display: block;
  width: 15px;
  height: 15px;
  border-bottom: 3px solid var(--primary-color);
  border-right: 3px solid var(--primary-color);
  transform: rotate(45deg);
  margin: -7px;
  animation: animate 2s infinite;
}

.scroll-arrow span:nth-child(2) {
  animation-delay: -0.2s;
}

.scroll-arrow span:nth-child(3) {
  animation-delay: -0.4s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-10px, -10px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(10px, 10px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* 響應式設計 */
@media (max-width: 768px) {
  .profile-section {
    flex-direction: column;
    text-align: center;
  }
  
  .profile-image {
    width: 150px;
    height: 150px;
  }
  
  .scroll-arrow-container {
    bottom: 10px;
  }
  
  .scroll-arrow span {
    width: 12px;
    height: 12px;
  }
}